.app {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  padding: 16px;
}

.app p {
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  padding: 24px;
  padding-top: 0;
  margin: 0;
}

.app textarea {
  flex: 1;
  max-width: 100%;
  min-width: 100%;
}

* {
  box-sizing: border-box;
}
